<template>
  <div class="wrap" v-bind:class="[condition.main]">
    <select v-model="selectCity">
      <option value="Sapporo">Êú≠Âπå</option>
      <option value="Akita">ÁßãÁî∞</option>
      <option value="Sendai">‰ªôÂè∞</option>
      <option value="Tokyo">Êù±‰∫¨</option>
      <option value="Nagoya">ÂêçÂè§Â±ã</option>
      <option value="Osaka">Â§ßÈò™</option>
      <option value="Hiroshima">Â∫ÉÂ≥∂</option>
      <option value="Fukuoka">Á¶èÂ≤°</option>
      <option value="Okinawa">Ê≤ñÁ∏Ñ</option>
    </select>
    <div class="inner">
      <p>{{ today }}{{ time }}</p>
      <div class="weather_city">{{ city }}</div>
      <div class="flex">
        <div class="weather-icon">
          <span
            v-if="condition.main == 'Clear'"
            class="fas fa-sun fa-2x"
          ></span>
          <span
            v-if="condition.main == 'Rain'"
            class="fas fa-cloud-rain fa-2x"
          ></span>
          <span
            v-if="condition.main == 'Clouds'"
            class="fas fa-cloud fa-2x"
          ></span>
          <span
            v-if="condition.main == 'Snow'"
            class="fas fa-snowflake fa-2x"
          ></span>
        </div>
        <div class="weather_condition">{{ condition.description }}</div>
      </div>
      <div class="weather_temp">{{ temp | formatTemp }}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      city: "",
      temp: "",
      condition: {
        main: "",
        description: "",
      },
      selectCity: "Tokyo",
      today: "",
      time: "",
    };
  },
  methods: {
    getDate: function() {
      var currentDate = new Date();
      //console.log(currentDate);
      var weeks = ["Êúà", "ÁÅ´", "Ê∞¥", "Êú®", "Èáë", "Âúü", "Êó•"];
      var week = currentDate.getDay() - 1;
      var getWeek = weeks[week];
      //console.log(weeks[week]);
      var getHour = String(
        currentDate.getHours() < 10
          ? "0" + currentDate.getHours()
          : currentDate.getHours()
      );
      var getMinute = String(
        currentDate.getMinutes() < 10
          ? "0" + currentDate.getMinutes()
          : currentDate.getMinutes()
      );
      var getSecond = String(
        currentDate.getSeconds() < 10
          ? "0" + currentDate.getSeconds()
          : currentDate.getSeconds()
      );
      this.today =
        currentDate.getFullYear() +
        "Âπ¥" +
        (currentDate.getMonth() + 1) +
        "Êúà" +
        currentDate.getDate() +
        "Êó•„ÄÄ" +
        getWeek +
        "ÊõúÊó•";
      this.time = "„ÄÄ" + getHour + ":" + getMinute + ":" + getSecond;
    },
  },
  mounted() {
    window.setInterval(() => {
      this.getDate();
    }, 1000);
  },
  watch: {
    selectCity: {
      handler(selectCity) {
        //console.log(selectCity);
        let apiURL =
          "https://api.openweathermap.org/data/2.5/forecast?q=" +
          this.selectCity +
          ",jp&units=metric&lang=ja&APPID=180cd81c3216710c472690ffec5655be";
        this.axios
          .get(apiURL)
          .then(
            //axios„ÅÆÂá¶ÁêÜ„ÅåÊàêÂäü„Åó„ÅüÂ†¥Âêà„Å´Âá¶ÁêÜ„Åï„Åõ„Åü„ÅÑ„Åì„Å®„ÇíË®òËø∞
            function(response) {
              this.city = response.data.city.name;
              this.temp = response.data.list[0].main.temp;
              this.condition = response.data.list[0].weather[0];
              console.log(response);
            }.bind(this)
            //this.axios„ÅÆÁ∂ö„Åç„Å´„ÅÇ„Çãfunction„Å™„ÅÆ„Åß„ÄÅthis„ÅÆÂÜÖÂÆπ„ÅåÂ§â„Çè„Çã„ÄÇ
            //function„ÅÆ‰∏≠„Å´„Åæ„Åüthis„Çí‰Ωø„ÅÑ„Åü„ÅÑ„Å™„Çâ„ÄÅ.bind(this)„Çí„Å§„Åë„Å™„ÅÑ„Å®„ÉÄ„É°
            //„ÇÇ„ÅÜ‰∏Ä„Å§„ÅÆËß£Ê±∫Á≠ñ„Å®„Åó„Å¶„ÄÅ„Ç≥„Éº„É´„Éê„ÉÉ„ÇØ„Çíarrow function„ÅßÊõ∏„Åè„Åì„Å®„ÇÇ„Åß„Åç„Çã
          )
          .catch(function(error) {
            //axios„ÅÆÂá¶ÁêÜ„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„ÅüÂ†¥Âêà„Å´Âá¶ÁêÜ„Åï„Åõ„Åü„ÅÑ„Åì„Å®„ÇíË®òËø∞
            console.log(error);
          });
      },
      immediate: true,
    },
  },
  filters: {
    formatTemp: function(val) {
      return (val = Math.round(val) + "¬∞");
    },
  },
};
</script>

<style scoped>
.wrap {
  background-size: cover;
  height: 100%;
  position: fixed;
  width: 100%;
  margin: 0;
}
.Rain {
  background-image: url(../assets/raining.jpg);
}
.Clear {
  background-image: url(../assets/sunny.jpg);
}
.Clouds {
  background-image: url(../assets/cloudy.jpg);
}
.Snow {
  background-image: url(../assets/snowy.jpg);
}
.inner {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -65%);
}
.weather_city {
  font-size: 3rem;
  font-weight: bold;
}
.weather_temp {
  font-size: 9rem;
  font-weight: bold;
}
.weather_condition {
  font-size: 2rem;
}
div {
  margin: 10px 5px;
  align-items: center;
}
p {
  letter-spacing: 1px;
}
.flex {
  display: flex;
  justify-content: center;
}
select {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  display: inline-block;
  width: 15%;
  margin: 1em 0;
  padding: 0.6em 0.2em;
  cursor: pointer;
  font-size: 0.95em;
  font-weight: 700;
  color: #fff;
  background-color: transparent;
  border: none;
  border-bottom: solid 1px #fff;
  /* üëáÂ∑¶„ÅÆ‰∏âËßí, Âè≥„ÅÆ‰∏âËßí */
  background-image: linear-gradient(45deg, transparent 50%, #fff 50%),
    linear-gradient(135deg, #fff 50%, transparent 50%);
  /* üëáÂ∑¶„ÅÆ‰∏âËßí„ÅÆ„Çµ„Ç§„Ç∫, Âè≥„ÅÆ‰∏âËßí„ÅÆ„Çµ„Ç§„Ç∫ */
  background-size: 5px 5px, 5px 5px;
  /* üëáÂ∑¶„ÅÆ‰∏âËßí„ÅÆ‰ΩçÁΩÆ, Âè≥„ÅÆ‰∏âËßí„ÅÆ‰ΩçÁΩÆ */
  background-position: calc(100% - 15px) 50%, calc(100% - 10px) 50%;
  /* üëá„É™„Éî„Éº„Éà„Åó„Å™„ÅÑ */
  background-repeat: no-repeat;
}
select:focus {
  outline: 0;
}
/* IE„Åß„Éá„Éï„Ç©„É´„Éà„ÅÆÁü¢Âç∞„ÇíÊ∂à„Åô */
select::-ms-expand {
  display: none;
}

option {
  background-color: #ccc;
  font-size: 0.95em;
  border: transparent;
}
</style>
